.social-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding-top: 17px;
  padding-bottom: 11px;
  width: 100%;
  border-top: $border-3;
  border-bottom: $border-3;

  @media (min-width: $tablet-width) {
    border: none;
    padding: 0;
  }
}

.social-list__item {
  margin-right: 25px;
  padding-bottom: 4px;

  &:last-child {
    margin-right: 0;
  }
}

.social-list__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  fill: $social-color;

  &:hover {
    fill: $bg-main;
  }

  &:active {
    opacity: 0.3;
  }

  &--facebook {
    @media (min-width: $tablet-width) {
      padding-right: 13px;
    }
  }
}

.social-list__icon {
  @media (min-width: $tablet-width) {
    transform: scale(1.4);
  }
}
